import React from 'react';

import yoga from '../images/yoga.jpg';

const YoganMed = () => {
  return (
    <div className='container'>
        <div className='card__body'>
            <img src={yoga} />
        </div>
        
            <h2>Yoga and Meditation Course:</h2>
            <div>
            Welcome to our Yoga and Meditation course, where we invite you to explore the 
            transformative power of these practices in nurturing your mind, body, and spirit. 
            Prepare to embark on a journey of self-discovery, holistic wellness, and inner peace.


            </div>

            <div>
                <div className='title'>Course Overview:</div>
                <div>
                    In this course, we'll delve into the profound teachings of Yoga and Meditation, 
                    drawing from centuries-old wisdom traditions to guide you on a path of personal 
                    growth and self-realization. Through a combination of gentle yoga asanas, breathwork,
                     meditation techniques, and mindfulness practices, you'll learn how to cultivate balance, 
                     harmony, and well-being in your life.
            

                </div>
            </div>

            <div>
                <div className='title'>What to Expect:</div>
                    <div>
                    <div><b>Yoga Asanas:</b> Explore a variety of yoga postures designed to increase flexibility, 
                    strength, and vitality, while promoting physical health and well-being.</div>
<div><b>Breathwork (Pranayama):</b> Discover the power of conscious breathing techniques to calm the mind,
 reduce stress, and enhance mental clarity and focus.</div>
<div><b>Meditation Practices:</b> Learn simple yet profound meditation techniques to quiet the mind,
 cultivate inner peace, and connect with your innermost self.</div>
<div><b>Mindfulness: </b>Cultivate present-moment awareness through mindfulness practices, 
allowing you to live more fully and authentically in each moment.</div>


                    </div>
            </div>


            <div>
                <div className='title'>Who Should Attend:</div>
                    <div>
                    This course is suitable for individuals of all ages and fitness levels who are seeking
                     to enhance their overall well-being and deepen their understanding of Yoga and Meditation.
                      Whether you're a beginner looking to establish a foundational practice or an experienced
                       practitioner seeking to deepen your understanding, this course offers something for
                        everyone. The course is open for the age group of 7 years and above.


                    </div>
            </div>

            <div>
                <div className='title'>Join Us:</div>
                    <div>
                    Embark on this enriching journey into the world of Yoga and Meditation and discover the profound
                     benefits of these practices for body, mind, and spirit. Prepare to cultivate a deeper 
                     sense of inner peace,
                     balance, and vitality as you journey towards greater self-awareness and holistic well-being.
                    </div>
            </div>


        
    </div>
  )
}

export default YoganMed







